 <pre class="ne-codeblock language-cpp" data-language="cpp" id="dfOR3"><span style="font-weight:bold;">6.2</span> <code>#include &lt;iostream&gt;
using namespace std;
int main ()
{
    int a=10, b=20, *p1=&amp;a, *p2=&amp;b;
    cout&lt;&lt;a&lt;&lt;'\t'&lt;&lt;*p1&lt;&lt;endl&lt;&lt;b&lt;&lt;'\t'&lt;&lt;*p2&lt;&lt;endl&lt;&lt;endl;
    int *p = p1;
    p1 = p2;
    p2 = p;
    cout&lt;&lt;a&lt;&lt;'\t'&lt;&lt;*p1&lt;&lt;endl&lt;&lt;b&lt;&lt;'\t'&lt;&lt;*p2&lt;&lt;endl&lt;&lt;endl;
    a=10, b=20, p1=&amp;a, p2=&amp;b;
    int temp = *p1;
    *p1 = *p2;
    *p2 = temp;
    cout&lt;&lt;a&lt;&lt;'\t'&lt;&lt;*p1&lt;&lt;endl&lt;&lt;b&lt;&lt;'\t'&lt;&lt;*p2&lt;&lt;endl&lt;&lt;endl;
    return 0;
}</code></pre>
